@font-face {
  font-family: 'Yandex Sans Text';
  src: url('../fonts/YandexSansText-Regular.woff2') format('woff2'), url('../fonts/YandexSansText-Regular.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Yandex Sans Text';
  src: url('../fonts/YandexSansText-Bold.woff2') format('woff2'), url('../fonts/YandexSansText-Bold.woff') format('woff');
  font-weight: bold;
  font-style: normal;
}

@font-face {
  font-family: 'Yandex Sans Text';
  src: url('../fonts/YandexSansText-Medium.woff2') format('woff2'), url('../fonts/YandexSansText-Medium.woff') format('woff');
  font-weight: 500;
  font-style: normal;
}
